
import { defineComponent } from "vue";

export default defineComponent({
  name: "customButton",
  props: {
    attr: {
      type: Object,
      default: () => ({}),
    },
    events: {
      type: Object,
      default: () => ({}),
    },
    data: {
      type: Object,
      default: () => ({}),
    },
  },
  setup(props) {
    return {
      ...props.events,
    };
  },
});
